
.forecast_wrapper {
    width: 100%;
}



.forecast_day_wrapper {
    display: flex;
    flex-wrap: nowrap;
    width: 200px;
    border: none;
    padding: 5px 5px;
    margin: 5px;
    border-radius: 5px;
    background: rgb(255, 255, 255);  /* fallback for old browsers */
  
}

.forecast_day_wrapper:hover {
    margin-bottom: 10px;
    margin-top: -10px;
    border-radius: 5px;
    box-shadow:rgba(0, 0, 0, 0.12) 0px 8px 16px 0px, rgba(0, 0, 0, 0.08) 0px 0px 1px 1px;
    box-sizing:border-box;
    color:rgb(34, 36, 38);
    transition-delay:0s, 0s;
    transition-duration:0.16s, 0.16s;
    transition-property:box-shadow, -webkit-box-shadow;
    transition-timing-function:cubic-bezier(0.25, 0.1, 0.25, 1), cubic-bezier(0.25, 0.1, 0.25, 1);
}

.day_info {
    width: 100%;
    flex-grow: 1;
    margin-top: 10px;
    border-top: 1px solid rgb(94, 94, 94);
    background: rgb(255, 255, 255);
}

.day_info_row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.fc_day__title {
    font-size: 18px;
    color: rgba(0, 0, 0, 0.8);
}
.fc_day_part__title {
    text-transform: capitalize;
    color: #939cb0;
    width: 15%;
}

.fc_info_title {
   color: #939cb0; 
   font-size: 15px;
   font-weight: 600;
}
.fc_info_value {
   color: rgba(0, 0, 0, 0.8); 
   font-size: 15px;
   font-weight: 600;
}
.fc_day_part__weather {
    width: 25%;
}


.fc_temp_day {
   color: #4e5055; 
   font-size: 25px;
}

.fc_temp_night {
   color: #939cb0; 
   font-size: 20px;
}

.fc_dayparts_wrapper {
    border-top: 1px thin #4e5055;
}

.test {
    width: 30em;
    height: 100%;
    background-color: coral;
}